import React, { useState } from "react";
import "./Progress.scss";
import { illustration, techStack } from "../../portfolio";
import { Fade } from "react-reveal";
import Build from "../../assets/lottie/build";
import DisplayLottie from "../../components/displayLottie/DisplayLottie";

export default function StackProgress() {
  const [openIndexes, setOpenIndexes] = useState([]); // store which categories are open

  const toggleCategory = (index) => {
    setOpenIndexes((prev) =>
      prev.includes(index) ? prev.filter((i) => i !== index) : [...prev, index]
    );
  };

  if (!techStack.viewSkillBars) return null;

  return (
    <Fade bottom duration={1000} distance="20px">
      <div className="skills-container">
        <div className="skills-bar">
          <h1 className="skills-heading">Proficiency</h1>
          {techStack.experience.map((exp, i) => {
            const progressStyle = { width: exp.progressPercentage };
            const isOpen = openIndexes.includes(i);

            return (
              <div key={i} className="skill-category">
                {/* Main category */}
                <div
                  className="skill-header"
                  onClick={() => toggleCategory(i)}
                  style={{ cursor: "pointer" }}
                >
                  <p className="skill-title">
                    {exp.Stack} ({exp.progressPercentage})
                  </p>
                  <span className={`toggle-icon ${isOpen ? "open" : ""}`}>
                    ▼
                  </span>
                </div>

                <div className="meter">
                  <span style={progressStyle}></span>
                </div>

                {/* Sub-skills */}
                {isOpen && exp.subSkills && exp.subSkills.length > 0 && (
                  <div className="subskills">
                    {exp.subSkills.map((sub, j) => {
                      const subProgressStyle = { width: sub.progressPercentage };
                      return (
                        <div key={j} className="subskill">
                          <p className="subskill-title">
                            {sub.name} ({sub.progressPercentage})
                          </p>
                          <div className="meter subskill-meter">
                            <span style={subProgressStyle}></span>
                          </div>
                        </div>
                      );
                    })}
                  </div>
                )}
              </div>
            );
          })}
        </div>

        <div className="skills-image">
          {illustration.animated ? (
            <DisplayLottie animationData={Build} />
          ) : (
            <img
              alt="Skills"
              src={require("../../assets/images/skill.svg")}
            />
          )}
        </div>
      </div>
    </Fade>
  );
}
